/* eslint-disable no-console */
import React, { ReactElement, useContext, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Notification } from 'components/Examples/Notification';
import { Button } from 'components/Button';
import { WelcomeTourModal } from 'components/WelcomeTourModal';
import { ThemeContext } from 'providers';
import styles from './Examples.module.scss';
import welcomeRocket from '../../assets/images/temp/welcome-rocket.svg';
/**
 * Home view
 */

export const Examples = (): ReactElement => {
  const { t } = useTranslation();
  const { currentTheme, themeSwitchHandler } = useContext(ThemeContext);

  const [tourModal, setTourModal] = useState(false);
  return (
    <div className='container'>
      <h1>{t('views.Examples.mainTitle')}</h1>
      <div className={styles.container}>
        <h1>The current theme is {currentTheme}</h1>
        <Button
          btnStyle='primary'
          text='Default'
          onClickCallback={() => themeSwitchHandler('default')}
        />
        <Button
          btnStyle='secondary'
          text='BSC'
          onClickCallback={() => themeSwitchHandler('bsc')}
        />
        <Button
          btnStyle='tertiary'
          text='Clover'
          onClickCallback={() => themeSwitchHandler('clover')}
        />
        <Notification title='This is an info Notification' />
        <Notification type='success' title='This is a succes Notification' />
        <Notification
          type='warning'
          title='This is a warning Notification'
          text='This is some example text'
        />
        <Notification type='error' title='This is a error Notification' />

        <Button
          onClickCallback={() => setTourModal(true)}
          text='Take the tour'
        />

        <WelcomeTourModal
          image={welcomeRocket}
          title='Welcome to your Health Corner App'
          isOpen={tourModal}
          onRequestClose={() => setTourModal(false)}
          // eslint-disable-next-line max-len
          text='In this 30-second guided tour we’ll introduce the most important features, so you can get started with ease.'
          buttonSecondary={{
            text: 'Skip Tour',
            btnStyle: 'tertiary',
            onClickCallback: () => {
              setTourModal(false);
            }
          }}
          buttonPrimaryType='link'
          buttonPrimary={{
            text: 'Start Tours',
            linkTo: 'http://www.google.com'
          }}
          isMobileButtonsReverse
        />
      </div>
    </div>
  );
};
