// Start Menu Icons
import { ReactComponent as CareCircle } from '../../assets/icons/menuicons/CareCircle.svg';
import { ReactComponent as CareFinder } from '../../assets/icons/menuicons/CareFinder.svg';
import { ReactComponent as CareResources } from '../../assets/icons/menuicons/CareResources.svg';
import { ReactComponent as Chat } from '../../assets/icons/menuicons/Chat.svg';
import { ReactComponent as HealthGoals } from '../../assets/icons/menuicons/HealthGoals.svg';
import { ReactComponent as HealthPicture } from '../../assets/icons/menuicons/HealthPicture.svg';
import { ReactComponent as MedicationManagment } from '../../assets/icons/menuicons/MedicationManagment.svg';
import { ReactComponent as Navigation } from '../../assets/icons/menuicons/Navigation.svg';
import { ReactComponent as ToDos } from '../../assets/icons/menuicons/ToDos.svg';
import { ReactComponent as Support } from '../../assets/icons/menuicons/Support.svg';
// End Menu Icons
// Start Chat Icons
import { ReactComponent as Call } from '../../assets/icons/call.svg';
import { ReactComponent as Search } from '../../assets/icons/search.svg';
import { ReactComponent as Video } from '../../assets/icons/video.svg';
// End Chat Icons
import { ReactComponent as WalgreensIcon } from '../../assets/icons/Walgreens_icon.svg';
import { ReactComponent as NoSVG } from '../../assets/icons/no-svg.svg';
import { ReactComponent as Delete } from '../../assets/icons/delete.svg';
import { ReactComponent as Dismiss } from '../../assets/icons/dismiss.svg';
import { ReactComponent as Undo } from '../../assets/icons/unselect-outlined.svg';
import { ReactComponent as Add } from '../../assets/icons/add.svg';
import { ReactComponent as ChevronUp } from '../../assets/icons/chevron-up.svg';
import { ReactComponent as ChevronDown } from '../../assets/icons/chevron-down.svg';
import { ReactComponent as ChevronLeft } from '../../assets/icons/chevron-left.svg';
import { ReactComponent as ChevronRightCircle } from '../../assets/icons/chevron-right-circle.svg';
import { ReactComponent as DatePicker } from '../../assets/icons/date-picker.svg';

// Cards
import { ReactComponent as RedCross } from '../../assets/icons/red-cross.svg';

// Med Management
import { ReactComponent as MedicationSkipped } from '../../assets/icons/Medication-schedule-skipped.svg';
import { ReactComponent as MedicationTaken } from '../../assets/icons/Medication-schedule-taken.svg';
import { ReactComponent as MedicationComingUp } from '../../assets/icons/Medication-schedule-comingup.svg';
import { ReactComponent as MedicationMissed } from '../../assets/icons/Medication-schedule-missed.svg';
import { ReactComponent as MedicationRested } from '../../assets/icons/Medication-schedule-rested.svg';
import { ReactComponent as MedicationAsNeededMarked } from '../../assets/icons/Medication-schedule-marked.svg';
import { ReactComponent as MedicationAsNeeded } from '../../assets/icons/Medication-schedule-passthrough.svg';
import { ReactComponent as UpcomingInfo } from '../../assets/icons/upcoming-info.svg';
import styles from './Icon.module.scss';

// List of available icons used in Storybook
export const iconTypes = [
  'care-circle',
  'care-finder',
  'care-resources',
  'chat',
  'health-goals',
  'health-picture',
  'medication-managment',
  'navigation',
  'to-dos',
  'call',
  'search',
  'video',
  'walgreens',
  'delete',
  'undo',
  'add',
  'chevron-up',
  'chevron-down',
  'chevron-left',
  'chevron-right-circle',
  'date-picker',
  'red-cross',
  'support',
  'medication-skipped',
  'medication-comming-up',
  'medication-missed',
  'medication-taken',
  'medication-rested',
  'medication-as-needed-marked',
  'medication-as-needed',
  'upcoming-info',
  'dismiss'
];
interface IconProps {
  /**
   * The name of the icon to load as inline SVG
   */
  type: string;

  /**
   * Text that is set as the title attribute of the SVG
   */
  alt?: string;
  /**
   * If active additional class is added to allow elements in SVG to change color.
   * Not all icons have an active state, changing this status will have no visual change.
   */
  isActive?: boolean;
}

const defaultIconProps = {
  isActive: false
};

export const pickIcon = (type: string) => {
  switch (type) {
    case 'care-circle':
      return CareCircle;
    case 'care-finder':
      return CareFinder;
    case 'care-resources':
      return CareResources;
    case 'chat':
      return Chat;
    case 'health-picture':
      return HealthPicture;
    case 'health-goals':
      return HealthGoals;
    case 'medication-managment':
      return MedicationManagment;
    case 'navigation':
      return Navigation;
    case 'to-dos':
      return ToDos;
    case 'call':
      return Call;
    case 'search':
      return Search;
    case 'video':
      return Video;
    case 'walgreens':
      return WalgreensIcon;
    case 'delete':
      return Delete;
    case 'undo':
      return Undo;
    case 'add':
      return Add;
    case 'chevron-up':
      return ChevronUp;
    case 'chevron-down':
      return ChevronDown;
    case 'chevron-left':
      return ChevronLeft;
    case 'chevron-right-circle':
      return ChevronRightCircle;
    case 'date-picker':
      return DatePicker;
    case 'red-cross':
      return RedCross;
    case 'support':
      return Support;
    case 'medication-skipped':
      return MedicationSkipped;
    case 'medication-comming-up':
      return MedicationComingUp;
    case 'medication-missed':
      return MedicationMissed;
    case 'medication-taken':
      return MedicationTaken;
    case 'medication-rested':
      return MedicationRested;
    case 'medication-as-needed-marked':
      return MedicationAsNeededMarked;
    case 'medication-as-needed':
      return MedicationAsNeeded;
    case 'upcoming-info':
      return UpcomingInfo;
    case 'dismiss':
      return Dismiss;
    default:
      return NoSVG;
  }
};

export const Icon = ({ alt, isActive, type }: IconProps): ReactElement => {
  const SVG = pickIcon(type);

  return (
    <span
      className={cn(styles['svg-wrapper'], { [styles.active]: isActive })}
      aria-hidden={!alt}
    >
      <SVG className={styles.svg} title={alt} />
    </span>
  );
};
Icon.defaultProps = defaultIconProps;
