import React, { useState, useEffect, useCallback } from 'react';

export const themeNames = ['default', 'bsc', 'clover'];
export const ThemeContext = React.createContext<{
  currentTheme: string;
  themeSwitchHandler(theme: string): void;
}>({
  currentTheme: '',
  themeSwitchHandler: () => {}
});

interface ProviderProps {
  children: any;
}

export const ThemeProvider = ({ children }: ProviderProps) => {
  const activeTheme: any =
    window.localStorage.getItem('h3ConsumerTheme') === null
      ? 'default'
      : window.localStorage.getItem('h3ConsumerTheme');
  const [currentTheme, setcurrentTheme] = useState(activeTheme);

  const loadTheme = (themeName: string) => {
    setcurrentTheme(themeName);
    themeNames.forEach((themeClass) => {
      document.documentElement.classList.remove(themeClass);
    });
    document.documentElement.classList.add(themeName);
    window.localStorage.setItem('h3ConsumerTheme', themeName);
  };

  const themeSwitchHandler = useCallback((themeToUse: string) => {
    loadTheme(themeToUse);
  }, []);
  useEffect(() => {
    themeSwitchHandler(currentTheme);
  }, [currentTheme, themeSwitchHandler]);
  return (
    <ThemeContext.Provider value={{ currentTheme, themeSwitchHandler }}>
      {children}
    </ThemeContext.Provider>
  );
};
