import * as React from 'react';
import { Link, LinkProps } from 'react-router-dom';

import cn from 'classnames';
import styles from './Button.module.scss';

export interface ButtonLinkProps {
  /**
   * Attribute to set the style of the button, actually with four style types
   */
  btnStyle?: 'primary' | 'secondary' | 'tertiary' | 'delete';
  /**
   * The link where the button will redirect us
   */
  linkTo: string;
  /**
   * Text that is showed inside the button
   */
  text: string;
  /**
   * The title of the button for accessibility reasons
   */
  title?: string;
  /**
   * To disable the button; adds the style disabled; setted as false by default
   */
  isDisabled?: boolean;
  /**
   * To control where is opened the content
   */
  target?: string;
  /**
   * Button to set an Anchor Link or React Link
   */
  isReactLink?: boolean;
}

const defaultButtonLinkProps = {
  btnStyle: 'primary',
  isDisabled: false,
  isReactLink: false
};

export const ButtonLink = ({
  linkTo,
  target,
  text,
  title,
  btnStyle = 'primary',
  isDisabled,
  isReactLink
}: ButtonLinkProps): React.ReactElement => {
  const Tag = isReactLink ? Link : 'a';

  const linksProps = {
    className: cn(styles.button, {
      [isDisabled
        ? [styles[btnStyle], styles.disabled].join(' ')
        : styles[btnStyle]]: btnStyle != null
    }),
    target,
    title,
    [isReactLink ? 'to' : 'href']: isDisabled ? null : linkTo
  } as unknown as LinkProps;

  return (
    <>
      {/* eslint-disable-next-line react/jsx-props-no-spreading */}
      <Tag {...linksProps}>{text}</Tag>
    </>
  );
};

ButtonLink.defaultProps = defaultButtonLinkProps;
