/* eslint-disable react/jsx-props-no-spreading */
import * as React from 'react';

import cn from 'classnames';
import styles from './Button.module.scss';

export interface ButtonProps {
  /**
   * Attribute to set the style of the button, actually with four style types
   */
  btnStyle?: 'primary' | 'secondary' | 'tertiary' | 'delete';
  /**
   * Text that is showed inside the button
   */
  text: string;
  /**
   * To disable the button; adds the style disabled; setted as false by default
   */
  isDisabled?: boolean;
  /**
   * Callback for the onClick event
   */
  onClickCallback?: (event: React.MouseEvent<HTMLButtonElement>) => void; // eslint-disable-line
  /**
   * The title of the button for accessibility reasons
   */
  title?: string;
  /**
   * Sets the button type; setted as default with 'button'
   */
  type?: 'button' | 'submit' | 'reset';
  /**
   * Test Id
   */
  testId?: string;
}

const defaultButtonProps = {
  btnStyle: 'primary',
  isDisabled: false,
  type: 'button'
};

export const Button = ({
  text,
  title,
  btnStyle = 'primary',
  onClickCallback,
  isDisabled,
  type,
  testId
}: ButtonProps): React.ReactElement => (
  <>
    <button
      title={title}
      type={type} // eslint-disable-line
      className={cn(styles.button, {
        [isDisabled
          ? [styles[btnStyle], styles.disabled].join(' ')
          : styles[btnStyle]]: btnStyle != null
      })}
      disabled={isDisabled}
      onClick={(event) => onClickCallback?.(event)}
      data-testid={testId}
    >
      {text}
    </button>
  </>
);

Button.defaultProps = defaultButtonProps;
