import React, { ReactElement } from 'react';
import { useTranslation } from 'react-i18next';
import { NavLink } from 'react-router-dom';
import cn from 'classnames';

import { Search } from '../Search';
import styles from './Header.module.scss';
/**
 * Header component
 */

export const Header = (): ReactElement => {
  const { t, i18n } = useTranslation();
  const activeLang: string = i18n.resolvedLanguage;
  const changeLanguage = (lang: string): void => {
    i18n.changeLanguage(lang);
    document.documentElement.lang = i18n.language;
  };

  return (
    <>
      <header className={styles.header}>
        <div className={styles.left}>
          <NavLink to='/'>{t('navigation.home')}</NavLink>
          <NavLink to='/login'>{t('navigation.login')}</NavLink>
          <NavLink to='/examples'>{t('navigation.examples')}</NavLink>
          <a href='/storybook/index.html'>Storybook</a>
        </div>
        <div className={cn([styles.right])}>
          <Search size='small' aria-controls='search-results' />
          <button
            className={cn(styles['language-button'], {
              [styles['active-language']]: activeLang === 'en'
            })}
            type='submit'
            onClick={() => changeLanguage('en')}
          >
            {t('navigation.lang.en')}
          </button>
          <button
            className={cn(styles['language-button'], {
              [styles['active-language']]: activeLang === 'es'
            })}
            type='submit'
            onClick={() => changeLanguage('es')}
          >
            {t('navigation.lang.es')}
          </button>
        </div>
      </header>
    </>
  );
};
