/* eslint-disable react/jsx-props-no-spreading */
import React, { ReactElement } from 'react';

import { Icon } from 'components/Icon';
import cn from 'classnames';
import { Link, LinkProps } from 'react-router-dom';
import styles from './TextBasedButton.module.scss';

export interface TextButtonLinkProps {
  /**
   * Attribute to set the style of the button, actually with two style types.
   */
  btnStyle?: 'primary' | 'delete';
  /**
   * The text that is showed in the text.
   */
  text: string;
  /**
   * The place where the icon is going to be showed; none and none-small only affects the text and
   * the icon is not showed.
   */
  iconPlace?: 'none' | 'none-small' | 'left' | 'right' | 'top';
  /**
   * Attribute to disable the button.
   */
  isDisabled?: boolean;
  /**
   * Icon to be showed when the iconPlace is not 'none' and 'none-small'.
   */
  icon?: string;
  /**
   * Title attribute of the button
   */
  title?: string;
  /**
   * The link where the button will redirect us
   */
  linkTo: string;
  /**
   *  Button to set an Anchor Link or React Link
   */
  isReactLink?: boolean;
  /**
   *  To control where is opened the content
   */
  target?: string;
  /**
   * Sets a dark background
   */
  isDarkMode?: boolean;
  /**
   * aria-label form more descriptive text for assitive technology.
   */
  ariaLabel?: string;
}

const defaultTextButtonProps = {
  btnStyle: 'primary',
  icon: 'search',
  iconPlace: 'none',
  isDisabled: false,
  isReactLink: false
};

export const TextButtonLink = ({
  btnStyle = 'primary',
  text,
  iconPlace = 'none',
  icon = 'search',
  isDisabled,
  isReactLink = false,
  linkTo,
  target,
  title,
  isDarkMode = false,
  ariaLabel
}: TextButtonLinkProps): ReactElement => {
  const isIconShowed = iconPlace !== 'none' && iconPlace !== 'none-small';
  const Tag = isReactLink ? Link : 'a';
  const linksProps = {
    className: cn(styles['text-button'], {
      [styles.disabled]: isDisabled
    }),

    [isReactLink ? 'to' : 'href']: isDisabled ? null : linkTo
  } as unknown as LinkProps;

  return (
    <>
      <Tag
        {...linksProps}
        {...(title && { title })}
        {...(target && { target })}
        {...(ariaLabel ? { 'aria-label': ariaLabel } : null)}
        data-mode={isDarkMode ? 'dark' : 'light'}
        data-place={iconPlace}
        data-style={btnStyle}
        data-disabled={isDisabled}
      >
        {isIconShowed && <Icon type={icon} />}
        <span className={styles.text}>{text}</span>
      </Tag>
    </>
  );
};

TextButtonLink.defaultProps = defaultTextButtonProps;
