import React, { ReactElement } from 'react';

import styles from './SkipToMainContent.module.scss';

export interface SkipToMainContentProps {
  /**
   * Id of the main content
   */
  mainContentId?: string;
  /**
   * Text of the link
   */
  children?: string;
}

const defualtSkipToMainContentProps = {
  mainContentId: 'main',
  children: 'Skip to Main Content'
};

export const SkipToMainContent = ({
  mainContentId,
  children
}: SkipToMainContentProps): ReactElement => (
  <a className={styles['skip-to-main']} href={`#${mainContentId}`}>
    {children}
  </a>
);

SkipToMainContent.defaultProps = defualtSkipToMainContentProps;
