import { ReactElement } from 'react';
import {
  Button,
  ButtonProps,
  ButtonLink,
  ButtonLinkProps
} from 'components/Button';
import { Modal, ModalProps } from 'components/Modal';
import styles from './WelcomeTourModal.module.scss';

interface WelcomeTourModalProps extends ModalProps {
  /**
   * Modal image
   */
  image: any;
  /**
   * Modal text
   */
  text: string;
  /**
   * Button Props same as ButtonProps
   * btnStyle?: 'primary' | 'secondary' | 'tertiary' | 'delete';
   * onClickCallback?: (event: React.MouseEvent<HTMLButtonElement>) => void;
   * text: string;
   * title?: string;
   * isDisabled?: boolean;
   * type?: 'button' | 'submit' | 'reset';
   */
  buttonPrimary?: ButtonProps | ButtonLinkProps;
  buttonPrimaryType?: 'button' | 'link';
  buttonSecondary?: ButtonProps | ButtonLinkProps;
  buttonSecondaryType?: 'button' | 'link';

  // ModalProps
  /**
   * Title text
   * title: string;
   */

  /**
    * String indicating how the content container should be announced
      to screenreaders. If not set, aria-labelledby is added and points to the title
      contentLabel?: string;
    */

  /**
   * Boolean describing if the modal should be shown or not.
   * isOpen: boolean;
   */

  /**
   * Function that will be run when the modal is requested
   * to be closed (either by clicking on overlay or pressing ESC).
   * Note: It is not called if isOpen is changed by other means.
   *
   *  onRequestClose: () => void;
   */

  /**
   * Function that will be run after the modal has opened.
   *
   *  onAfterOpen?: () => void;
   */

  /**
   * Function that will be run after the modal has closed.
   *
   * onAfterClose?: () => void;
   */

  /**
    * Number indicating the milliseconds to wait before closing
      the modal.

       closeTimeoutMS?: number;
    */

  /**
   * String id to be applied to the content div
   *
   * id?: string;
   */

  /**
    * String testId that renders a data-testid attribute in the DOM,
     useful for testing. 

     testId?: string;
    */

  /**
   * children?: Array<React.ReactElement> | React.ReactElement;
   * */

  /**
   * Boolean to hide page content from assistive technologies when modal is open
   *
   *  ariaHideApp?: boolean;
   */
}

export const WelcomeTourModal = ({
  image,
  text,
  buttonPrimary,
  buttonPrimaryType = 'button',
  buttonSecondary,
  buttonSecondaryType = 'button',
  title,
  contentLabel,
  children,
  isOpen,
  isMobileButtonsReverse,
  onRequestClose,
  onAfterOpen,
  onAfterClose,
  closeTimeoutMS,
  id,
  testId,
  ariaHideApp
}: WelcomeTourModalProps): ReactElement => {
  const buttonType = (type: string, props: any): JSX.Element => {
    if (type === 'link') {
      return <ButtonLink {...props} />;
    }
    return <Button {...props} />;
  };
  return (
    <Modal
      title={title}
      contentLabel={contentLabel}
      isOpen={isOpen}
      onRequestClose={() => onRequestClose()}
      onAfterOpen={() => onAfterOpen?.()}
      onAfterClose={() => onAfterClose?.()}
      closeTimeoutMS={closeTimeoutMS}
      id={id}
      testId={testId}
      ariaHideApp={ariaHideApp}
      isMobileButtonsReverse={isMobileButtonsReverse}
    >
      {image && (
        <Modal.Slot slotName='image-top'>
          <img src={image} alt='' aria-hidden />
        </Modal.Slot>
      )}
      <p className={styles.text}>{text}</p>
      {children}
      <Modal.Slot slotName='button'>
        {buttonSecondary && buttonType(buttonSecondaryType, buttonSecondary)}
        {buttonPrimary && buttonType(buttonPrimaryType, buttonPrimary)}
      </Modal.Slot>
    </Modal>
  );
};
