import React, { useRef, Children } from 'react';
import ReactModal from 'react-modal';
import cn from 'classnames';
import { ReactComponent as CloseModal } from '../../assets/icons/modal/modal-close.svg';
import styles from './Modal.module.scss';

// Conditionally setAppElement for testing purposes
const root = document.getElementById('root');
if (root) {
  ReactModal.setAppElement('#root');
}
interface SlotProps {
  slotName: 'button' | 'image-top';
  mobileOrder?: 'primary-bottom' | 'primary-top';
}

const Slot: React.FC<SlotProps> = () => null;

export interface ModalProps {
  /**
   * Title text
   */
  title: string;
  /**
   * String indicating how the content container should be announced
     to screenreaders. If not set, aria-labelledby is added and points to the title
   */
  contentLabel?: string;
  /**
   * Boolean describing if the modal should be shown or not.
   */
  isOpen: boolean;

  /**
   * Boolean to reverse order of buttons on mobile
   */
  isMobileButtonsReverse?: boolean;

  /**
   * Function that will be run when the modal is requested
   * to be closed (either by clicking on overlay or pressing ESC).
   * Note: It is not called if isOpen is changed by other means.
   */
  onRequestClose: () => void;
  /**
   * Function that will be run after the modal has opened.
   */
  onAfterOpen?: () => void;
  /**
   * Function that will be run after the modal has closed.
   */
  onAfterClose?: () => void;
  /**
   * Number indicating the milliseconds to wait before closing
     the modal.
   */
  closeTimeoutMS?: number;
  /**
   * String id to be applied to the content div
   */
  id?: string;
  /**
   * String testId that renders a data-testid attribute in the DOM,
    useful for testing. 
   */
  testId?: string;

  children?: Array<React.ReactElement> | React.ReactElement;
  /**
   * Boolean to hide page content from assistive technologies when modal is open
   */
  ariaHideApp?: boolean;
}
const ModalDefaultProps = {
  closeTimeoutMS: 0,
  ariaHideApp: false
};

export const Modal = ({
  title,
  contentLabel,
  children,
  isOpen,
  isMobileButtonsReverse,
  onRequestClose,
  onAfterOpen,
  onAfterClose,
  closeTimeoutMS,
  id,
  testId,
  ariaHideApp = true
}: ModalProps) => {
  const childrenArray = Children.toArray(
    children
  ) as unknown as React.ReactElement[];

  const buttonSlot = childrenArray.find(
    (child) => child?.props?.slotName === 'button'
  );

  let reverseButtonSlot: {} | null | undefined = [];
  if (isMobileButtonsReverse && buttonSlot?.props?.children) {
    reverseButtonSlot = Object.values(buttonSlot?.props?.children).reverse();
  }

  const imageSlot = childrenArray.find(
    (child) => child?.props?.slotName === 'image-top'
  );

  const firstFocus = useRef<HTMLButtonElement>(null);
  const newAfterOpen = () => {
    firstFocus?.current?.focus();
    onAfterOpen?.();
  };

  return (
    <>
      <ReactModal
        contentLabel={contentLabel || title}
        isOpen={isOpen}
        onRequestClose={() => onRequestClose()}
        className={styles['modal-window']}
        overlayClassName={styles['modal-overlay']}
        shouldCloseOnOverlayClick={false}
        onAfterOpen={() => {
          newAfterOpen();
        }}
        onAfterClose={onAfterClose}
        closeTimeoutMS={closeTimeoutMS}
        id={id}
        testId={testId}
        ariaHideApp={ariaHideApp}
      >
        <button
          type='button'
          className={styles['close-button']}
          onClick={() => onRequestClose()}
          ref={firstFocus}
        >
          <span aria-hidden className={styles['close-svg']}>
            <CloseModal />
          </span>
          Close
        </button>

        {imageSlot?.props?.children && (
          <figure className={styles['header-image']}>
            {imageSlot?.props?.children}
          </figure>
        )}

        <h2
          className={cn(styles.heading, {
            [styles['heading-with-image']]: imageSlot?.props?.children
          })}
        >
          {title}
        </h2>

        {children}
        {buttonSlot?.props?.children && (
          <div className={styles['buttons-container']}>
            <div
              className={cn(styles.buttons, styles['buttons-desktop'], {
                [styles['buttons-desktop-all']]: isMobileButtonsReverse !== true
              })}
            >
              {buttonSlot?.props?.children}
            </div>
            {isMobileButtonsReverse && (
              <div className={cn(styles.buttons, styles['buttons-mobile'])}>
                {reverseButtonSlot}
              </div>
            )}
          </div>
        )}
      </ReactModal>
    </>
  );
};
Modal.Slot = Slot;
Modal.defaultProps = ModalDefaultProps;
