import { useRef, useState, useEffect } from 'react';

import cn from 'classnames';
import { TextButton } from 'components/TextBasedButton';
import { Icon } from '../Icon';
import styles from './Search.module.scss';

interface SearchProps {
  /**
   * The box that the search controls
   */
  'aria-controls'?: string;
  /**
   * The class to override styles
   */
  className?: string;
  /**
   * Puts the search bar open by default
   */
  isOpen?: boolean;
  /**
   * Sets the size of the search bar
   */
  size?: 'small' | 'medium' | 'large' | 'extra-large';
  /**
   * The onclick function for the search button
   */
  onClick?: (event: React.MouseEvent<HTMLButtonElement>) => void; // eslint-disable-line
  /**
   * The label to be displayed for accessibility requirements
   */
  onChange?: (event: any) => void; // eslint-disable-line
  /**
   * The placeholder of the search input
   */
  placeholder?: string;
}

export const Search = ({
  'aria-controls': ariaControls,
  className,
  isOpen = false,
  size = 'medium',
  onClick,
  onChange,
  placeholder
}: SearchProps) => {
  const [open, setOpen] = useState(isOpen);
  const search = useRef<HTMLInputElement>(null);

  const onClickClose = () => {
    setOpen((o) => !o);
  };

  useEffect(() => {
    const parent = search.current?.parentElement;

    const onFocusOutHandler = (event: FocusEvent): any => {
      if (!parent?.contains(event.relatedTarget as Node)) setOpen((o) => !o);
    };

    const clickCallback = (event: MouseEvent) => {
      if (!parent?.contains(event.target as Node)) setOpen((o) => !o);
    };
    if (open) {
      search?.current?.focus();
      window.addEventListener('click', clickCallback);
      parent?.addEventListener('focusout', onFocusOutHandler);
    } else {
      window.removeEventListener('click', clickCallback);
      parent?.removeEventListener('focusout', onFocusOutHandler);
    }
    return () => {
      window.removeEventListener('click', clickCallback);
      parent?.removeEventListener('focusout', onFocusOutHandler);
    };
  }, [open]);

  return (
    <>
      <form
        role='search'
        className={`${[styles.search]} ${className}`}
        data-open={open}
      >
        <button
          aria-label='Search button'
          className={`${styles.button}`}
          data-search-element='search-button'
          title='Search'
          type='button'
          onClick={onClick}
        >
          <Icon type='search' alt='Search' />
        </button>
        <input
          aria-label='Search field'
          className={`${styles.input}`}
          data-size={size}
          data-search-element='input'
          type='search'
          placeholder={placeholder}
          role='combobox'
          aria-controls={ariaControls}
          aria-expanded='false'
          onChange={onChange}
          ref={search}
        />
        <div data-search-element='close-button' className={cn([styles.close])}>
          <TextButton
            title='Close search bar'
            type='button'
            iconPlace='left'
            icon='dismiss'
            text='Close'
            onClickCallback={onClickClose}
          />
        </div>
      </form>
      <div
        data-search-element='open-search'
        className={cn(styles.search)}
        data-open={!open}
      >
        <TextButton
          title='Open search bar'
          type='button'
          iconPlace='left'
          icon='search'
          text='Search'
          isDarkMode
          onClickCallback={onClickClose}
        />
      </div>
    </>
  );
};

Search.defaultProps = {
  isOpen: false,
  size: 'm',
  placeholder: 'Search',
  className: ''
};
